import { IntlShape } from "react-intl";

/**
 * Formats hours/minutes/seconds.
 */
// eslint-disable-next-line import/prefer-default-export
export function formatDurationWithSeconds(
  seconds: number,
  intl: IntlShape
): string {
  return intl.formatMessage(
    {
      defaultMessage:
        utils.defaultMessages[
          "otpUi.TransitVehicleOverlay.durationWithSeconds"
        ],
      description: "Formats a duration in hours, minutes, and seconds",
      id: "otpUi.TransitVehicleOverlay.durationWithSeconds"
    },
    coreUtils.time.toHoursMinutesSeconds(seconds)
  );
}

export function FormattedDurationWithSeconds({
  seconds
}: {
  seconds: number;
}): React.ReactElement {
  const intl = useIntl();
  return formatDurationWithSeconds(seconds, intl);
}
